.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(92, 92, 92);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
  
}

.p-browser {
  height: 20px;
  background-color: rgb(92, 92, 92);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: rgb(224, 222, 222);
}

.p-img {
  width: 100%;
}




