.i {
  display: flex;
  height: 100vh;
  background: linear-gradient(#cc9bb3,#eebfbf,#cc9bb3,#eebfbf );
}

.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  
  font-size: 30px;
  font-weight: 300;
}

.i-name {
  font-size: 60px;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-50px);
  }
  
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #59b256;
  display: flex;
  align-items: center;
}



.i-img {
  width: 100%;
  height: 100%;
  object-fit: scale-down;
  position: absolute;
}

.i-bg {
  clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: #00457E;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}


